html {
  box-sizing: border-box;
  background: #eee;
  font-family: helvetica neue;
  font-size: 20px;
  font-weight: 200;
}

body {
  margin: 0;
}

*, :before, :after {
  box-sizing: inherit;
}

.site-wrap {
  max-width: 700px;
  text-align: justify;
  background: #fff;
  margin: 70px auto;
  padding: 40px;
  transition: transform .5s;
  transform: scale(.98);
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, .05);
}

header {
  text-align: center;
  height: 50vh;
  background: url("https://source.unsplash.com/GKN6rpDr0EI/960x640") bottom / cover no-repeat;
  justify-content: center;
  align-items: center;
  display: flex;
}

h1 {
  color: #fff;
  text-shadow: 3px 4px rgba(0, 0, 0, .2);
  font-size: 7vw;
}

nav {
  width: 100%;
  z-index: 1;
  background: #000;
  transition: all .5s;
  position: relative;
  top: 0;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav li {
  text-align: center;
  flex: 1;
  justify-content: center;
  align-items: center;
  display: flex;
}

li.logo {
  max-width: 0;
  background: #fff;
  font-size: 30px;
  font-weight: 600;
  transition: all .5s;
  overflow: hidden;
}

li.logo a {
  color: #000;
}

nav a {
  color: #fff;
  text-transform: uppercase;
  padding: 20px;
  text-decoration: none;
  transition: all .2s;
  display: inline-block;
}

